@import url("https://fonts.googleapis.com/css2?family=Goldman&display=swap");

.navbar {
  list-style: none;
  display: flex;
  border-bottom: 1px solid white;
  width: 80vw;
  margin: auto;
  font-family: "Goldman", cursive;
}
.navbar li {
  cursor: pointer;
  padding: 0 30px;
  height: 100px;
  font-size: 20px;
  color: white;
  line-height: 100px;
}
.navbar li:hover {
  border-bottom: 5px solid rgb(56, 94, 217);
}
.loginBt {
  position: relative;
  left: 300px;
}
